import React from "react";
import DocumentTitle from "react-document-title";
import PageIndex from './../../widgets/pageindex/index.js';

import './servicepage.css';

export default class ServicePage extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
  }

  render() {
    var that = this;
    var { lang } = that.common.data;

    var pageindex_array = [];
    pageindex_array.push({ label: lang.homepage_title, value: '/' });
    pageindex_array.push({ label: lang.servicepage_title, value: '/service' });
    return (
      <DocumentTitle title={`${lang.servicepage_title}${lang.document_title}`}>
        <div className="servicepage">
          <PageIndex data={pageindex_array} />
          asgsdg
          
        </div>
      </DocumentTitle>
    )
  }
}


